ont-family:'Raleway', sans-serif;font-size:16px;display:flex;align-items:center;justify-content:center;position:fixed;width:100vw;height:100vh;bottom:0;right:0;background-color:rgba(0,0,0,0.8);z-index:2;visibility:hidden;opacity:0;overflow:hidden;transition:.64s ease-in-out}.image-card-modal__popup-inner{position:relative;bottom:-100vw;right:-100vh;display:flex;align-items:center;max-width:800px;max-height:600px;width:60%;height:80%;background-color:#fff;-webkit-transform:rotate(32deg);transform:rotate(32deg);transition:.64s ease-in-out}.image-card-modal__popup__photo{display:flex;justify-content:flex-end;align-items:flex-end;width:40%;height:100%;overflow:hidden}.image-card-modal__popup__photo img{height:100%;width:100%}.image-card-modal__popup__text{display:flex;flex-direction:column;justify-content:flex-start;width:60%;height:100%;padding:4rem;overflow:auto}.image-card-modal__popup__text h1{font-size:2rem;font-weight:600;margin-bottom:2rem;text-transform:uppercase;color:#0A0A0A}.image-card-modal__popup__text p{font-size:.875rem;color:#686868;line-height:1.5}.image-card-modal__popup--active{visibility:visible;opacity:1}.image-card-modal__popup--active .image-card-modal__popup-inner{bottom:0;right:0;-webkit-transform:rotate(0);transform:rotate(0)}.image-card-modal__popup__close{position:absolute;right:-1rem;top:-1rem;width:3rem;height:3rem;font-size:.875rem;font-weight:300;border-radius:100%;background-color:#0A0A0A;z-index:4;color:#fff;line-height:3rem;text-align:center;cursor:pointer;text-decoration:none}

